import React, { useRef, useEffect, useState } from "react";

type Props = {
  children: React.ReactNode;
  threshold?: number; // ì–¼ë§ˆë‚˜ ë³´ì—¬ì•¼ ë“±ì¥í• ì§€ (ê¸°ë³¸: 0.1)
  delay?: number;     // ì• ë‹ˆë©”ì´ì…˜ ì§€ì—° ì‹œê°„ (ms)
};

export function FadeInSection({ children, threshold = 0.1, delay = 0 }: Props) {
  const ref = useRef<HTMLDivElement | null>(null);
  const [isVisible, setVisible] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setVisible(true);
          observer.disconnect(); // í•œ ë²ˆë§Œ ì‹¤í–‰ (ì¬ë“±ì¥ ë°©ì§€ ì‹œ ì œê±° ê°€ëŠ¥)
        }
      },
      { threshold }
    );

    if (ref.current) observer.observe(ref.current);

    return () => observer.disconnect();
  }, [threshold]);

  return (
    <div
      ref={ref}
      style={{
        opacity: isVisible ? 1 : 0,
        transform: isVisible ? "translateY(0px)" : "translateY(30px)",
        transition: `opacity 0.8s ease-out ${delay}ms, transform 0.8s ease-out ${delay}ms`,
        willChange: "opacity, transform",
      }}
    >
      {children}
    </div>
  );
}
